						<va-button v-if="row?.rowData?.is_latest==='0'||row?.rowData?.is_latest===0" title="ÁÇπÂáªÂèëÂ∏ÉËØ•ÁâàÊú¨" size="small" color="rgb(47, 148, 172)" icon="rocket" @click="confirmPublish(row?.rowData?.version)">ÂèëÂ∏É</va-button>
