					/>
                    <VaInput
                        v-model="editForm.download_url"
                        label="‰∏ãËΩΩÈìæÊé•"
                        type="string"
                        class="mb-3"
                    />	
					<VaInput
					    v-model="editForm.release_notes"
					    label="Êõ¥Êñ∞Êó•Âøó (Â¶ÇÈúÄÊç¢Ë°åËØ∑Áî®','ÈöîÂºÄ)"
					    type="textarea"
					    class="mb-3"
