				ok-text="üöÄ Á°ÆËÆ§ÂèëÂ∏É"
				cancel-text="ÊöÇ‰∏çÂèëÂ∏É"
			    @ok="handleConfirm"
			    @cancel="handleCancel"
			  >
				<template #header>
				  <div class="dialog-header">
					<va-icon name="warning" color="warning" />
					<span class="header-text">ÁâàÊú¨ÂèëÂ∏ÉÁ°ÆËÆ§</span>
