				  </p>
				  <va-divider />
				  <p class="confirm-text">ËØ•Êìç‰ΩúÂ∞ÜÁ´ãÂç≥ÁîüÊïà‰∏î‰∏çÂèØÈÄÜËΩ¨ÔºåËØ∑Á°ÆËÆ§ÊòØÂê¶ÁªßÁª≠Ôºü</p>
				</div>
