<script setup>
import { ref, reactive,computed } from 'vue'
import { getPoolProfits, updatePoolProfit } from "../../api/node"
import { versionList, addVersion, modifyVersion, publishVersion } from "../../api/version"
import { formatDateTime,formatHashRate,convertDateTimeToDate } from "../../utils/date.ts"
import { useToast } from "vuestic-ui"

const { init: toast } = useToast()

// ÂìçÂ∫îÂºèÁä∂ÊÄÅ
// Ëé∑ÂèñÈªòËÆ§Êó•ÊúüÔºàÂΩìÂâçÊó•ÊúüÂáè‰∏ÄÂ§©Ôºâ

const currencyOptions = ref([
    { value: 'aleo', text: 'Aleo' },
    // ÂÖ∂‰ªñÂ∏ÅÁßçÈÄâÈ°π
])

const currentStartIndex = ref(1)
const totalItems = ref(0)
const versions = ref([])
const loading = ref(false)

const showConfirmDialog = ref(false)
const pendingVersion = ref(null)

const confirmPublish = (version) => {
  pendingVersion.value = version
  showConfirmDialog.value = true
  
  return new Promise((resolve) => {
	resolvePromise.value = resolve
  })
}

const handleConfirm = async () => {
  try {
	await publishNewVersion(pendingVersion.value)
	resolvePromise.value(true)
  } catch (error) {
	resolvePromise.value(false)
  } finally {
	showConfirmDialog.value = false
  }
}

const handleCancel = () => {
  toast({ message: 'Â∑≤ÂèñÊ∂àÂèëÂ∏É', color: 'warning' })
  resolvePromise.value(false)
  showConfirmDialog.value = false
}

const validateVersion = (value) => {
  const pattern = /^v\d+\.\d+\.\d+(-[a-zA-Z0-9]+)?$/
  return pattern.test(value) || 'Ê†ºÂºèÁ§∫‰æãÔºöv1.0.0 Êàñ v2.3.4-beta'
}

// URLÊ†ºÂºèÈ™åËØÅ
const validateURL = (value) => {
  try {
    new URL(value)
    return true
  } catch {
    return 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑURLÂú∞ÂùÄ'
  }
}

const requiredRule = (value) => !!value || 'Ê≠§Â≠óÊÆµÂøÖÂ°´'

const formValid = computed(() => {
  return validateVersion(addForm?.version) === true &&
         validateURL(addForm?.download_url) === true
})

const queryParams = reactive({
    page: 1,
    pagesize: 15,
})

const isAddModalVisible = ref(false)
const addForm = reactive({
	version: '',
	download_url: '',
	release_notes: ''
})

const addVersionForm = () => {
    isAddModalVisible.value = true
}

const truncateText = (text, maxLength) => {
      if (!text) return ''
      return text.length > maxLength 
        ? text.slice(0, maxLength) + '...' 
        : text
}

// ‰øÆÊîπ fetchData ÊñπÊ≥ï
const fetchData = async () => {
    loading.value = true
    try {
        const params = {
            page: queryParams.page,
            pagesize: queryParams.pagesize
        }

        const res = await versionList(params)
        // Âõ†‰∏∫Êã¶Êà™Âô®Â∑≤ÁªèÂ§ÑÁêÜ‰∫Ü res.data ÁöÑËß£ÊûÑÔºåËøôÈáåÁõ¥Êé•‰ΩøÁî®ËøîÂõûÂÄº
        if (res?.versions) {
            versions.value = res.versions
            totalItems.value = res.total
        } else {
            versions.value = []
            totalItems.value = 0
        }
    } catch (error) {
        console.error("Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:", error)
        toast({
            message: error.message || "Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•",
            color: "danger",
        })
        versions.value = []
        totalItems.value = 0
    } finally {
        loading.value = false
    }
}

const columns = [
    { key: 'version', label: 'ÁâàÊú¨Âè∑' },
	{ key: 'release_date', label: 'ÂèëÂ∏ÉÊó•Êúü' },
    { key: 'download_url', label: '‰∏ãËΩΩÈìæÊé•' },
    { key: 'release_notes', label: 'Êõ¥Êñ∞Êó•Âøó' },
    { key: 'is_latest', label: 'ÂèëÂ∏ÉÁä∂ÊÄÅ' },
    { key: 'actions', label: 'Êìç‰Ωú' }  // Ê∑ªÂä†Êìç‰ΩúÂàó
]

const isEditModalVisible = ref(false)
const editForm = reactive({
	version: '',
	release_notes: '',
	download_url: ''
})

const editProfit = (row) => {
    editForm.version = row.rowData.version
	editForm.download_url = row.rowData.download_url
    editForm.release_notes = row.rowData.release_notes
    isEditModalVisible.value = true
}

const onOk = async () => {
    try {
        const msg = await modifyVersion(editForm.version, {
            download_url: editForm.download_url,
            release_notes: editForm.release_notes
        })
        toast({
            message: msg,
            color: "success",
        })
        await fetchData()
        isEditModalVisible.value = false
    } catch (error) {
        console.error("‰øÆÊîπÂ§±Ë¥•:", error)
        toast({
            message: error.message || "‰øÆÊîπÂ§±Ë¥•",
            color: "danger",
        })
    }
}

const publishNewVersion = async (version) => {
    try {
        const msg = await publishVersion(version)
		toast({
		  message: msg,
		  color: 'success',
		  duration: 3000
		})
        await fetchData()
    } catch (error) {
        toast({
            message: error.message || "ÂèëÂ∏ÉÂ§±Ë¥•",
            color: "danger",
			duration: 5000
        })
    }
}

const onAddOk = async () => {
	if (formValid.value) {
		try {
		    const msg = await addVersion({
		        version: addForm.version,
		        download_url: addForm.download_url,
		        release_notes: addForm.release_notes
		    })
		    toast({
		        message: msg,
		        color: "success",
		    })
		    await fetchData()
		    isAddModalVisible.value = false
		} catch (error) {
		    console.error("‰øÆÊîπÂ§±Ë¥•:", error)
		    toast({
		        message: error.message || "‰øÆÊîπÂ§±Ë¥•",
		        color: "danger",
		    })
		}
	}
}

const onCancel = () => {
    isEditModalVisible.value = false
    resetForm()
}

const onAddCancel = () => {
    isAddModalVisible.value = false
    reseyAddForm()
}

const resetForm = () => {
    editForm.version = ''
    editForm.download_url = ''
    editForm.release_notes = ''
}

const reseyAddForm = () => {
	addForm.version = ''
	addForm.download_url = ''
	addForm.release_notes = ''
}

// ÂàÜÈ°µ
const handlePageChange = (startIndex) => {
    queryParams.page = Math.ceil(startIndex / queryParams.pagesize)
    currentStartIndex.value = startIndex
    fetchData()
}

const refreshList = () => {
    fetchData()
}

const refreshData = () => {
    fetchData()
}


// ÂàùÂßãÂåñÂä†ËΩΩÊï∞ÊçÆ
fetchData()
</script>

